import React, { useEffect, useState } from 'react';
import { useContext } from 'react';
import { AuthContext } from '../../context/AuthContext';
import axios from 'axios';
import ToastConfig from '../../component/toast/ToastConfig';

const countItemPage = 50

const HistoryTable = ({ tableHeader, urlApi }) => {
  const { user } = useContext(AuthContext)
  const [history, setHistory] = useState([])
  const [historyShow, setHistoryShow] = useState([...history])
  const [values, setValues] = useState([])
  const [indexShow, setIndexShow] = useState(-1)
  const [fromTime, setFromTime] = useState("")
  const [toTime, setToTime] = useState("")
  const [page, setPage] = useState(1)
  if (typeof tableHeader[0].name === 'number') {
    tableHeader[0].name = historyShow.length
  } else {
    tableHeader.unshift({
      name: historyShow.length,
      status: false,
    })
  }

  const n = Math.ceil(historyShow.length / countItemPage)
  let arrayPage = []

  if (page >= 2 && page < n) arrayPage = [page - 1, page, page + 1]
  else if (page === 1) arrayPage = [1, 2, 3]
  else arrayPage = [n - 2, n - 1, n]

  if (page > 2) {
    arrayPage.unshift(null)
    arrayPage.unshift(1)
  }

  if (page < n - 1) {
    arrayPage.push(null)
    arrayPage.push(n)
  }

  useEffect(() => {
    getHistory('2000-01-01 00:00', '2040-01-01 00:00')
  }, [])
  useEffect(() => {
    const tmp = []
    tableHeader.forEach(element => {
      tmp.push({ st: element.status })
    });
    history.forEach((element) => {
      Object.keys(element).forEach((item, index) => {
        if (tmp[index + 1].st) {
          tmp[index + 1][element[item]] = true
        }
      })
    });
    tmp.forEach((element) => {
      delete element.st
    })
    setValues(tmp)
    console.log('tmp', tmp);
  }, [history])
  useEffect(() => {
    if (values.length > 0) {
      const tmp = []
      history.forEach((elementHistory) => {
        let status = true
        Object.keys(elementHistory).forEach((elementKey, index) => {
          if (Object.keys(values[index + 1]).length > 0) {
            const val = elementHistory[elementKey]
            if (status) {
              if (!(values[index + 1][val])) status = false
            } else return
          }
        });
        if (status) {
          tmp.push(elementHistory)
        }
      })
      setHistoryShow(tmp)
    }
  }, [values])

  const getHistory = (from, to) => {
    from ??= fromTime.replace(/T/g, " ")
    to ??= toTime.replace(/T/g, " ")
    if (from === '') {
      ToastConfig('không để trống trường from', 'warning')
    } else if (to === '') {
      ToastConfig('không để trống trường to', 'warning')
    } else if (from >= to) {
      ToastConfig('khoảng thời gian from phải nhỏ hơn to', 'warning')
    } else {
      axios.get(`${urlApi}?${'name=' + user.name}${'&password=' + user.password}${'&from=' + from}${'&to=' + to}`)
        .then(response => {
          if (response.data.status) {
            const data = response.data.data
            data.reverse()
            setHistory(data)
          }
        })
        .catch(error => {
          console.error(error);
        });
    }
  }
  return (
    <div className='gap-4'>
      <div className="max-w-[1200px] relative mx-auto">
        <div className='flex gap-3 mt-4'>
          <span>from</span>
          <input className='w-48 border' type="datetime-local" value={fromTime} onChange={(e) => setFromTime(e.target.value)} />
          <span>to</span>
          <input className='w-48 border' type="datetime-local" value={toTime} onChange={(e) => setToTime(e.target.value)} />
          <button onClick={() => getHistory()} className='active:scale-95 bg-blue-400 px-3 rounded-md text-white'>get</button>
        </div>
        <table className="w-full text-sm text-left text-gray-600 mt-4 shadow-md sm:rounded-lg">
          <thead className="text-xs text-gray-700 uppercase bg-gray-50">
            <tr>
              {tableHeader.map((item, index) => (
                <th scope="col" className="px-6 py-3">
                  <div className='flex relative'>
                    <div className='flex cursor-pointer select-none' onClick={() => setIndexShow(index === indexShow ? -1 : index)}>
                      <p >{item.name}</p>
                      {item.status && (<i className="ml-2 fa-solid fa-caret-down"></i>)}
                    </div>
                    {item.status && index === indexShow && (<div>
                      <ul className='absolute top-7 left-0 lowercase bg-white px-2 shadow-config rounded-lg min-w-[70px]'>
                        {values[index] && Object.keys(values[index]).map((item) => (
                          <li onClick={() => setValues(prop => {
                            const tmp = [...prop]
                            const tmpRow = { ...prop[index] }
                            tmpRow[item] = !values[index][item]
                            tmp[index] = tmpRow
                            console.log(tmp);
                            return tmp
                          })} className={`border-b-2  py-2 select-none cursor-pointer ${values[index][item] ? 'font-bold' : 'font-normal'}`}>{item}</li>
                        ))}
                      </ul>
                    </div>)}
                  </div>
                </th>
              ))}
            </tr>
          </thead>
          <tbody className='border-t-2'>
            {historyShow.slice(countItemPage * (page - 1), countItemPage * page).map((item, index) => (
              <tr key={index} className="whitespace-nowrap bg-white w-[100px] border-b ">
                <th className="px-6 py-4 font-medium text-gray-900 whitespace-nowrap">
                  {index + 1 + countItemPage * (page - 1)}
                </th>
                {tableHeader.slice(1).map((itemTitle, index) => (
                  <td className="px-6 py-4">
                    {item[itemTitle.name]}
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
        <div className='flex flex-wrap gap-2 mt-3 pb-10 justify-center'>
          {/* {Array.from({ length: Math.ceil(historyShow.length / countItemPage) }, (_, index) => index + 1).map((item, index) => (
            <div onClick={() => setPage(item)} className={`w-6 h-6 text-xs shadow-md select-none cursor-pointer rounded-full flex justify-center items-center ${page === item ? 'bg-blue-500 text-white' : 'bg-white'}`}>{item}</div>
          ))} */}
          {arrayPage.map((item, index) => (
            <div key={index} onClick={() => {
              if (item) {
                setPage(item)
              }
            }} className={`w-6 h-6 text-xs shadow-md select-none cursor-pointer rounded-full flex justify-center items-center ${page === item ? 'bg-blue-500 text-white' : 'bg-white'}`}>{item ? item : "..."}</div>
          ))}
        </div>
      </div>
    </div >
  );
};

export default HistoryTable;