import React from 'react';
import { useState } from 'react';
import History from '../history/History';
import { HistoryChart, HistoryTable } from '../../component';

const DataSensor = () => {

  const [type, setType] = useState(0)
  return (
    <div className='max-w-[1200px] relative mx-auto pt-3'>
      <div className='flex justify-center gap-3 mb-3'>
        <button onClick={() => setType(0)} className={`px-3 py-2 rounded-lg  ${type === 0 ? 'bg-blue-400 text-white' : 'border'}`}>Đồ thị</button>
        <button onClick={() => setType(1)} className={`px-3 py-2 rounded-lg  ${type === 1 ? 'bg-blue-400 text-white' : 'border'}`}>Bảng</button>
      </div>
      {type === 0 ?
        <HistoryChart />
        : <HistoryTable tableHeader={[{
          name: 'time',
          status: false,
        }, {
          name: 'name',
          status: true,
        }, {
          name: 'value',
          status: false,
        }]} urlApi={'https://apimanage1306.000webhostapp.com/getHistory.php'} />}
    </div>
  );
};

export default DataSensor;