            <BlockCode codeString={`// kết nối tới mqtt
useEffect(() => {
  client = mqtt.connect(connectUrl, {
    clean: true,
    connectTimeout: 4000,
    clientId: 'emqx_test' + Math.random().toString(),
    //dùng tài khoản và mật khẩu để kết nối tới mqtt
    username: user.nameMqtt,
    password: user.passwordMqtt
  })
  if (status) {
    //đăng kí topic để lắng nghe thông điệp từ iot device
    client.on('connect', () => {
      client.subscribe('s-c', (error) => {
      });
    });

    let timeoutId;
    client.on('message', (topic, message) => {
      clearTimeout(timeoutId);
      //hiển thị thông báo nếu đã kết nối được với esp32
      setStatusConnectIot(prop => {
        if (!prop) {
          ToastConfig('đã kết nối tới iot device', 'success')
        }
        return true
      })
      timeoutId = setTimeout(() => {
        //hiển thị thông báo nếu mất kết nối tới esp32 (sau 4s không nhận được thông điệp từ esp32)
        setStatusConnectIot(prop => {
          if (prop) {
            ToastConfig('đã mất kết nối tới iot device', 'warning')
          }
          return false
        })
      }, 4000);
      // chuyển thông điệp trả về từ esp32 thành json
      const dataArray = JSON.parse(message.toString());
      const Array = [];
      const switchsArray = [];

      // chia mảng trả về thành 2 mảng (mảng gồm các thiết bị công tắc , các thiết bị cảm biến)
      dataArray.forEach(item => {
        if (item.unit === null) switchsArray.push(item);
        else Array.push(item);
      });

      setNewData(Array)
      setSwitchs(switchsArray)
    });
  }
  handleGetLimit()
  return () => {
    // ngắt kết nối mqtt nếu component bị unmout
    client.end();
  }
}, [status, timeChange])` } language={'javascript'} path={'/src/page/home/Home.js'} />
